/*
BUTTONS
*/
.ant-btn {
    color: aliceblue;
    margin : 1em 0;
    font-size: 0.8em;
    background-color: #009688;
    border-color: #004D40;
    border-radius: 7px;
    text-shadow: 0 -1px 0 rgba(236, 232, 232, 0.12);
    box-shadow: 0 2px 0 rgba(240, 237, 237, 0.045);
}

.ant-btn:hover, .ant-btn:focus {
    background: #078073;
    border: 1px solid #009688;
}

.ant-btn-default {
  color: black;
  background-color: #eee;
}

.ant-btn-default:hover, .ant-btn-default:focus {
  color: aliceblue;
}

.trash {
    background: transparent;
    border: none;
    font-size: 1em;
    margin: 0.8em 0;
    cursor: pointer;
}

.trash:hover, .trash:focus {
    background: transparent;
    border: none;
}

button {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 50px;
}

.form > button {
  transition: 0.5s;
  font-size: 0.9em;
  padding: 0;
}

#buttonSubmit {
  border-radius: 50%;
}

.formButton-active {
  background: #26A69A;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

